.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    padding-top: 80px;
    width: 80%;
}

@media screen and (max-width: 1090px) {
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}

.hr {
    margin: 30px;
}